<template>
  <div>
    <el-card>
      <div slot="header" class="clearfix">
        <span>成品{{ cardTitle }}</span>
      </div>
      <el-form
        class=""
       style="padding: 0 60px 0 20px; width: 700px;margin:60px 0; "
        ref="formRef"
        :model="form"
        :rules="formRules"
        label-width="none"
      >
        <div
          v-for="(item, index) in form.response_datas"
          :key="index + ' onlyItem'"
        >
          <el-form-item
            :prop="'response_datas.' + index + '.pack_id'"
            :rules="[]"
            label="包装编码"
          >
            <el-input
              :ref="'finishedProductCode' + index"
              v-model="item.pack_id"
              @input="
                nextFocus(
                  'finishedProductCode' + (index + 1),
                  index,
                  item.pack_id
                )
              "
            ></el-input>
          </el-form-item>
        </div>
      </el-form>

      <div>
        <el-button @click="resetForm">重 置</el-button>
        <!-- <el-button type="primary" @click="dialogSave">确 定</el-button> -->
      </div>
    </el-card>
  </div>
</template>

<script>
import { baseManagement } from "@/request/api";
import { mapState, mapActions } from "vuex";

export default {
  name: "MissStation",
  computed: {
    ...mapState(["ProductionPlanCode"]),
  },
  props: {
    formStatus: {
      type: String,
      default: "",
    },
    cardTitle: {
      type: String,
      default: "",
    },
  },
  data() {
    return {
      timeout: "",
      Shipping_SN_length: "",
      // 产品过站弹出层
      form: {
        product_plan_code: "",
        status: "",
        response_datas: [
          {
            pack_id: "",
          },
        ],
      },
      formBase: {
        product_plan_code: "",
        status: "",
        response_datas: [
          {
            pack_id: "",
          },
        ],
      },
      formRules: {},
    };
  },
  methods: {
    ...mapActions(["getLogisticProductPlanDeal"]),
    nextFocus(nextRef, index, matter_code) {
      if (this.timeout) {
        clearTimeout(this.timeout);
      }
      this.timeout = setTimeout(() => {
        if (this.form.response_datas[0].length != this.Shipping_SN_length) {
          this.$message.warning(
            "包装编码长度为" + this.Shipping_SN_length + "个字节。"
          );
          // this.finished_product_codeFlag = false;
          return false;
        }
        this.dialogSave();
        // if (index < this.form.response_datas.length - 1) {
        //   this.$nextTick(() => {
        //     this.$refs[nextRef][0].focus();
        //   });
        // } else {
        //   console.log(5789);
        // }
      }, 500);
    },

// 打印参数列表 查询
    getLogisticOperationSystem() {
      baseManagement
        .getLogisticOperationSystem({
          page: 1,
          page_size: 10,
        })
        .then((res) => {
          console.log("打印参数列表 查询", res);
          if (res.code == 0) {
            this.Package_Qty = res.data.data[0].Package_Qty;
            this.Shipping_SN_length = res.data.data[0].Shipping_SN_length;
            this.Product_length = res.data.data[0].Product_length;

            console.log("this.Package_Qty ", this.Package_Qty);
          } else {
            this.$message({
              message: res.message,
              type: "error",
            });
          }
        });
    },
    // 产品过站 新增
    getLogisticstockInStockOut(params) {
      baseManagement.getLogisticstockInStockOut(params).then((res) => {
        console.log("产品过站 新增", res);
        if (res.code == 0) {
          this.form = JSON.parse(JSON.stringify(this.formBase));
          this.$message({
            message: res.message,
            type: "success",
          });
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },

    // 重置按钮
    resetForm() {
      this.form = JSON.parse(JSON.stringify(this.formBase));
    },

    // 确认按钮
    dialogSave() {
      console.log("this.form", this.form);
      if (!this.ProductionPlanCode) {
        this.$message.warning("没有进行中的生产计划！");
        return false;
      }

      this.$refs.formRef.validate((valid) => {
        if (!valid) {
          this.$message({
            message: "请修改正确的数据格式！",
            type: "warning",
          });
          return;
        } else {
          //验证通过调用保存接口
          console.log("产品过站", this.form);
          let params = JSON.parse(JSON.stringify(this.form));
          params.status = this.formStatus;
          params.product_plan_code = this.ProductionPlanCode;
          params.response_datas = JSON.stringify(this.form.response_datas);
          console.log("paramsparams", params);
          this.getLogisticstockInStockOut(params);
        }
      });
    },
  },
  mounted() {
    // 打印参数列表 查询
    this.getLogisticOperationSystem();
    // 进行中的生产计划接口：
    this.getLogisticProductPlanDeal();

    this.$nextTick(() => {
      this.$refs.firstInput.focus();
    });
  },
};
</script>

<style lang="scss" scoped>
.boxDiv span {
  font-size: 14px;
}
.el-form-item {
  // margin: 0;
  /deep/ .el-form-item__label {
    font-size: 24px !important;
  }
  /deep/ .el-input__inner {
    font-size: 24px !important;
  }
  /deep/ .el-form-item__error {
    font-size: 16px !important;
  }
}
</style>